import React, { useState } from 'react';
import { Badge } from '@/components/ui/badge';
import { ChevronDown, ChevronUp, ExternalLink } from 'lucide-react';

interface OrderItem {
  id: number;
  type: 'stock' | 'preorder';
  sku: string | null;
  item_name: string | null;
  size: string;
  price_cny: string;
  price_rub: string;
  status: string;
  dewu_url: string | null;
  shipping_type: string | null;
  category_type: string | null;
  weight_category: string | null;
  color_code: string | null;
  quantity: number;
}

interface OrderPromocode {
  promocode_text: string;
  discount_fixed: number | null;
  discount_percent: string | null;
}

interface Order {
  order_id: number;
  created_at: string;
  items: OrderItem[];
  prepay_amount: string;
  status: string;
  promocode: OrderPromocode | null;
  subtotal: string;
  final_price: string;
  discount_amount: string;
  dd_coins_used: string;
  delivery_type: string;
  delivery_price: string;
  delivery_address: string | null;
  delivery_info: string | null;
  delivery_code: string | null;
}

interface OrderCardProps {
  order: Order;
}

const OrderCard: React.FC<OrderCardProps> = ({ order }) => {
  const [isExpanded, setIsExpanded] = useState(false);

  const getStatusColor = (status: string): string => {
    switch (status.toLowerCase()) {
      case 'delivered':
      case '–¥–æ—Å—Ç–∞–≤–ª–µ–Ω':
        return 'bg-green-500';
      case 'awaiting manager':
      case '–æ–∂–∏–¥–∞–µ—Ç –º–µ–Ω–µ–¥–∂–µ—Ä–∞':
        return 'bg-yellow-500';
      case 'awaiting purchase':
      case '–æ–∂–∏–¥–∞–µ—Ç –ø–æ–∫—É–ø–∫–∏':
        return 'bg-blue-500';
      case 'cancelled':
      case '–æ—Ç–º–µ–Ω—ë–Ω':
        return 'bg-red-500';
      default:
        return 'bg-gray-500';
    }
  };

  const translateStatus = (status: string): string => {
    switch (status.toLowerCase()) {
      case 'delivered':
        return '–î–æ—Å—Ç–∞–≤–ª–µ–Ω';
      case 'awaiting manager':
        return '–û–∂–∏–¥–∞–µ—Ç –º–µ–Ω–µ–¥–∂–µ—Ä–∞';
      case 'awaiting purchase':
        return '–û–∂–∏–¥–∞–µ—Ç –ø–æ–∫—É–ø–∫–∏';
      case 'cancelled':
        return '–û—Ç–º–µ–Ω—ë–Ω';
      default:
        return status;
    }
  };

  const formatPrice = (price: string | number | null): string => {
    if (!price) return '0';
    const numericPrice = typeof price === 'string' ? parseFloat(price) : price;
    return numericPrice.toLocaleString('ru-RU', { maximumFractionDigits: 2 });
  };

  const formatDate = (dateString: string): string => {
    const date = new Date(dateString);
    return date.toLocaleString('ru-RU', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  const getItemsCountText = (count: number): string => {
    // Rules for Russian pluralization
    if (count % 10 === 1 && count % 100 !== 11) {
      return '—Ç–æ–≤–∞—Ä';
    } else if ([2, 3, 4].includes(count % 10) && ![12, 13, 14].includes(count % 100)) {
      return '—Ç–æ–≤–∞—Ä–∞';
    } else {
      return '—Ç–æ–≤–∞—Ä–æ–≤';
    }
  };

  return (
    <div className="bg-white dark:bg-sidebar-accent rounded-lg shadow-sm overflow-hidden">
      {/* Order Summary - Always Visible */}
      <div 
        className="p-4 cursor-pointer hover:bg-gray-50 dark:hover:bg-sidebar-primary/10 transition-colors"
        onClick={() => setIsExpanded(!isExpanded)}
      >
        <div className="flex justify-between items-start">
          <div>
            <div className="flex items-center gap-2">
              <h3 className="text-lg font-medium">–ó–∞–∫–∞–∑ #{order.order_id}</h3>
              <Badge className={`${getStatusColor(order.status)} text-white`}>
                {translateStatus(order.status)}
              </Badge>
            </div>
            <p className="text-sm text-gray-500 dark:text-gray-400 mt-1">
              {formatDate(order.created_at)}
            </p>
            <p className="text-sm text-gray-600 dark:text-gray-400 mt-1">
              {order.items.length} {getItemsCountText(order.items.length)}
            </p>
          </div>
          <div className="text-right">
            <p className="font-medium text-lg">‚ÇΩ{formatPrice(order.final_price)}</p>
            {order.promocode && (
              <p className="text-sm text-telegram-blue">
                {order.promocode.promocode_text}
              </p>
            )}
            <button 
              className="mt-2 text-telegram-blue hover:text-telegram-dark transition-colors"
              aria-label={isExpanded ? '–°–≤–µ—Ä–Ω—É—Ç—å –¥–µ—Ç–∞–ª–∏' : '–†–∞–∑–≤–µ—Ä–Ω—É—Ç—å –¥–µ—Ç–∞–ª–∏'}
            >
              {isExpanded ? <ChevronUp size={20} /> : <ChevronDown size={20} />}
            </button>
          </div>
        </div>
      </div>

      {/* Order Details - Expandable */}
      {isExpanded && (
        <div className="border-t border-gray-100 dark:border-gray-800/50">
          <div className="p-4 space-y-4">
            {/* Order Items */}
            {order.items.map((item, index) => (
              <div 
                key={`item-${index}-${item.id}`} 
                className="border-b dark:border-gray-700 pb-3 last:border-0"
              >
                <div className="flex justify-between items-start">
                  <div>
                    <div className="flex flex-wrap gap-2 mb-1">
                      <Badge className="bg-blue-100 text-blue-800 dark:bg-blue-900/30 dark:text-blue-300">
                        {item.type === 'stock' ? '–°—Ç–æ–∫' : '–ü—Ä–µ–¥–∑–∞–∫–∞–∑'}
                      </Badge>
                      {item.status && (
                        <Badge className={`${getStatusColor(item.status)} text-white`}>
                          {translateStatus(item.status)}
                        </Badge>
                      )}
                    </div>
                    
                    <p className="font-medium">
                      {item.item_name || (item.dewu_url ? (
                        <a 
                          href={item.dewu_url} 
                          target="_blank" 
                          rel="noopener noreferrer"
                          className="text-telegram-blue hover:underline flex items-center"
                        >
                          {item.dewu_url.substring(0, 30)}{item.dewu_url.length > 30 ? '...' : ''}
                          <ExternalLink size={14} className="ml-1" />
                        </a>
                      ) : '–¢–æ–≤–∞—Ä')}
                    </p>
                    
                    <p className="text-sm text-gray-600 dark:text-gray-400">
                      –†–∞–∑–º–µ—Ä: {item.size} {item.quantity > 1 && `√ó ${item.quantity}`}
                    </p>
                    
                    {item.type === 'preorder' && item.price_cny && (
                      <p className="text-sm text-gray-600 dark:text-gray-400">
                        –¶–µ–Ω–∞ (—é–∞–Ω–∏): ¬•{formatPrice(item.price_cny)}
                      </p>
                    )}
                    
                    {item.sku && (
                      <p className="text-sm text-gray-600 dark:text-gray-400">
                        –ê—Ä—Ç–∏–∫—É–ª: {item.sku}
                      </p>
                    )}
                    
                    {item.shipping_type && (
                      <p className="text-sm text-gray-600 dark:text-gray-400">
                        –¢–∏–ø –¥–æ—Å—Ç–∞–≤–∫–∏: {item.shipping_type}
                      </p>
                    )}
                    
                    {item.category_type && (
                      <p className="text-sm text-gray-600 dark:text-gray-400">
                        –ö–∞—Ç–µ–≥–æ—Ä–∏—è: {item.category_type}
                      </p>
                    )}
                    
                    {item.weight_category && (
                      <p className="text-sm text-gray-600 dark:text-gray-400">
                        –í–µ—Å: {item.weight_category}
                      </p>
                    )}
                    
                    {item.color_code && (
                      <div className="flex items-center gap-2 text-sm text-gray-600 dark:text-gray-400">
                        <span>–¶–≤–µ—Ç:</span>
                        <div 
                          className="w-4 h-4 rounded-full border border-gray-300" 
                          style={{ backgroundColor: item.color_code }}
                        ></div>
                      </div>
                    )}
                  </div>
                  
                  <div className="text-right">
                    <p className="font-medium">‚ÇΩ{formatPrice(item.price_rub)}</p>
                  </div>
                </div>
              </div>
            ))}

            {/* Order Summary */}
            <div className="pt-3 border-t dark:border-gray-700">
              <div className="flex justify-between text-sm mb-1">
                <span className="text-gray-600 dark:text-gray-400">–°—É–º–º–∞ —Ç–æ–≤–∞—Ä–æ–≤</span>
                <span>‚ÇΩ{formatPrice(order.subtotal)}</span>
              </div>

              {order.delivery_code && (
                <div className="flex justify-between text-sm mb-1">
                  <span className="text-gray-600 dark:text-gray-400">
                    –î–æ—Å—Ç–∞–≤–∫–∞: {order.delivery_code}
                  </span>
                  <span>
                    {order.delivery_price === "0" ? 
                      "–ë–µ—Å–ø–ª–∞—Ç–Ω–æ" : 
                      `‚ÇΩ${formatPrice(order.delivery_price)}`}
                  </span>
                </div>
              )}

              {order.promocode && (
                <div className="flex justify-between text-sm text-telegram-blue mb-1">
                  <span>
                    –ü—Ä–æ–º–æ–∫–æ–¥: {order.promocode.promocode_text}
                    {order.promocode.discount_percent && order.promocode.discount_percent !== 'null' && 
                      ` (-${order.promocode.discount_percent}%)`}
                    {order.promocode.discount_fixed && 
                      ` (-${order.promocode.discount_fixed}‚ÇΩ)`}
                  </span>
                  <span>-‚ÇΩ{formatPrice(order.discount_amount)}</span>
                </div>
              )}

              {order.discount_amount && order.discount_amount !== "0" && !order.promocode && (
                <div className="flex justify-between text-sm text-telegram-blue mb-1">
                  <span>–°–∫–∏–¥–∫–∞</span>
                  <span>-‚ÇΩ{formatPrice(order.discount_amount)}</span>
                </div>
              )}

              {order.dd_coins_used && order.dd_coins_used !== "0" && (
                <div className="flex justify-between text-sm text-yellow-500 mb-1">
                  <span>–ò—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–æ DD –ö–æ–∏–Ω–æ–≤</span>
                  <span>-‚ÇΩ{formatPrice(order.dd_coins_used)}</span>
                </div>
              )}

              {order.prepay_amount && order.prepay_amount !== "0" && (
                <div className="flex justify-between text-sm mb-1">
                  <span className="text-gray-600 dark:text-gray-400">–ü—Ä–µ–¥–æ–ø–ª–∞—Ç–∞</span>
                  <span>‚ÇΩ{formatPrice(order.prepay_amount)}</span>
                </div>
              )}

              <div className="flex justify-between font-medium text-base mt-2">
                <span>–ò—Ç–æ–≥–æ</span>
                <span>‚ÇΩ{formatPrice(order.final_price)}</span>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default OrderCard; 